.digitar_post {
    @apply h-36 p-2 resize-none mt-5 rounded-lg;
    background-color: #e1e1e1;
}

.acoes_postar {
    @apply self-end;
    
}

.acoes_postar .selecionar_imagem {
    @apply w-9 h-9 mr-2;
}

.publicar {
    @apply flex-shrink-0  text-white p-2 rounded-lg mt-3;
    background-color: #545454;
}

