.tipos_conta {
    display: flex;
    justify-content: center;
    column-gap: 125px;
}

.tipo {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    width: 125px;
    height: 160px;
    border: 3px solid black;
    border-radius: 15px;
    padding: 5px;
}

.tipos_conta img {
    width: 125px;
}

.tipos_conta span {
    margin: 0 auto;
    font-weight: 700;
    font-size: 18px;
}

.tipos_conta .selecionado span {
    color:#780ba4;
}

.tipos_conta .selecionado {
    border: 3px solid #780ba4;
}